body {
    background-image: linear-gradient(120deg, #7B7F7F 0%, #448A7E 100%);
}

.app {
    width: 350px;
    height: 500px;
    background: #2B4D47;
    margin: 50px auto;
}

.app h1 {
    color: #57867E;
    text-align: center;
    padding: 20px 0;
}

.app form {
    width: 80%;
    margin: 0 auto 30px;
}

.app form input {
    width: 100%;
    padding: 5px;
    margin-bottom: 5px;
    outline: 0;
}

.app form button {
    color: white;
    background-color: #57867E;
    margin-top: 10px;
    padding: 7px 15px;
    cursor: pointer;
    outline: 0;
    border: 0;
    transition: background-color .5s;
}

.app form button:hover {
    background-color: #30423f;
}

.app .view {
    width: 80%;
    margin: 10px auto;
}

.app .view > div {
    color: #ccdddb;
    padding: 10px 10px 5px;
    border-bottom: 1px solid #52726d;
}

.app .error {
    color: red;
    width: 80%;
    margin: 10px auto;
}